import Parler from "./index";

export default class CommentFormatService {

    static getUserFromId(userId, userList) {
        for (let loop = 0; loop < userList.length; loop++) {
            let user = userList[loop];
            if (user.id === userId)
                return user;
        }
        return null;
    }

    static getUserFromName(userName, userList) {
        for (let loop = 0; loop < userList.length; loop++) {
            let user = userList[loop];
            if (user.username === userName)
                return user;
        }
        return null;
    }

    static formatComment(comment, userList, user) {
        let setViewingUserId = (comment, user) => {
            if (!comment) return comment;
            comment.viewingUserId = user._id;
            return comment;
        };
        let setUserData = (comment, user) => {
            comment.userData = user;
            return comment;
        };
        let setCreatedUserData = (comment, userList, defaultUser) => {
            if (!comment) return comment;
            if (!comment.creator) comment.creator = defaultUser.id;
            let createdUserData = CommentFormatService.getUserFromId(comment.creator, userList);
            comment.createdUserData = createdUserData;
            return comment;
        };
        let setProfileId = (comment, userList) => {
            if (!comment) return comment;
            if ((!userList) || (!userList.length)) return;
            for (let loop2 = 0; loop2 < userList.length; loop2++) {
                if (comment.creator !== userList[loop2].id) continue;
                comment.profilePhotoId = userList[loop2].profilePhoto;
            }
            if (!comment.profilePhotoId) {
                console.log("PARLER LOGGING WARNING: No user Profile Photo for creator id "  + comment.creator);
            }
            return comment;
        };
        let formatDeletedComment = (comment) => {
            if (!comment.deleted) return comment;
            comment.profilePhotoId = null;
            comment.creatorUserName = 'COMMENT DELETED BY USER';
            console.log("PARLER LOGGING Deleted comment found: " + Parler.debugPrint(comment, false));
            return comment;
        };
        setProfileId(comment, userList);
        setViewingUserId(comment, user);
        setUserData(comment, user);
        setCreatedUserData(comment, userList, user);
        formatDeletedComment(comment);
        return comment;
    }
    static formatComments(commentList, userList, user) {
        if (!commentList || !commentList.length) return;
        for (let loop = 0; loop < commentList.length; loop++) {
            commentList[loop] = this.formatComment(commentList[loop], userList, user);
        }
        return commentList;
    }
}



