import React from 'react';
import ReactDOM from 'react-dom';
import {OverlayTrigger, Glyphicon, Popover} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-theme.css';
import './comment-component.css';
import './comment-dropdown-component.css';

class CustomToggle extends React.Component {
    constructor(props, context) {
        super(props, context);

        this.handleClick = this.handleClick.bind(this);
    }

    handleClick(e) {
        e.preventDefault();

        this.props.onClick(e);
    }

    render() {
        return (
            <a href="" onClick={this.handleClick}>
                {this.props.children}
            </a>
        );
    }
}

class CustomMenu extends React.Component {
    constructor(props, context) {
        super(props, context);

        this.handleChange = this.handleChange.bind(this);

        this.state = {
            value: ''
        };
    }

    handleChange(e) {
        this.setState({ value: e.target.value });
    }

    focusNext() {
        const input = ReactDOM.findDOMNode(this.input);

        if (input) {
            input.focus();
        }
    }

    render() {
        const { children } = this.props;
        const { value } = this.state;

        return (
            <div className="dropdown-menu" style={{ padding: '' }}>
                <ul className="list-unstyled">
                    {React.Children.toArray(children).filter(
                        child => !value.trim() || child.props.children.indexOf(value) !== -1
                    )}
                </ul>
            </div>
        );
    }
}

export default class CommentDropdownComponent extends React.Component {
    constructor(props) {
        super(props);
    }
    editComment() {
        this.props.editFunc();
    }
    deleteComment() {
        this.props.deleteFunc();
    }
    goAnonymous() {
        this.props.goAnonymousFunc();
    }
    render() {
        const popoverHoverFocus = (
            <Popover id="popover-trigger-hover-focus">
                <div eventKey="1" onClick={() => { this.editComment(); }}>Edit</div>
                <div eventKey="2" onClick={() => { this.deleteComment(); }}>Delete</div>
                <div eventKey="3" onClick={() => { this.goAnonymous(); }}>Go anonymous</div>
            </Popover>
        );

        return(
            <OverlayTrigger
                trigger={['click']}
                placement="bottom"
                overlay={popoverHoverFocus}
            >
                <img className={'chevron'} src="http://parler.com/assets/more.png"/>
            </OverlayTrigger>
        );
    }
}



