import React from 'react';
import {Modal, Grid, Row, Col, Button, Alert} from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-theme.css';
import './login-access-code.css';

export default class LoginAccessCode extends React.Component {
    constructor(props) {
        super(props);
        this.accessCode1Ref = React.createRef();
        this.accessCode2Ref = React.createRef();
        this.accessCode3Ref = React.createRef();
        this.accessCode4Ref = React.createRef();
        this.accessCode5Ref = React.createRef();
        this.accessCode6Ref = React.createRef();
        this.state = {error: '', info: ''};
    }
    onSubmitAccessCode() {
        let accessCode = this.accessCode1Ref.current.value
            + this.accessCode2Ref.current.value
            + this.accessCode3Ref.current.value
            + this.accessCode4Ref.current.value
            + this.accessCode5Ref.current.value
            + this.accessCode6Ref.current.value;
        this.props.accessCodeSubmitFunc(accessCode);
    }
    onCancelAccessCode() {
        this.props.accessCodeCancelFunc();
    }
    onResendEmail() {
        this.setState(...this.state, {error: 'The email has been resent. Don\'t forget to check your spam folder.'});
        this.props.emailSubmitFunc(this.props.signInEmail);
    }
    onPaste(event, editBox) {
        event.preventDefault();
        event.stopPropagation();
        event.clipboardData.items[0].getAsString( text => {
            if ((!text) || (!text.length)) return;
            if (1 === text.length) {
                editBox.current.value = text;
                this.setState(...this.state, {error: ''});
                return;
            } else if (6 !== text.length) {
                this.setState(...this.state, {error: 'Access code must be six characters in length.'});
            }
            this.pasteText(text);
        });
    }
    pasteText(text) {
        if (6 !== text.length) return;
        this.setState(...this.state, {error: ''});
        this.accessCode1Ref.current.value = text[0];
        this.accessCode2Ref.current.value = text[1];
        this.accessCode3Ref.current.value = text[2];
        this.accessCode4Ref.current.value = text[3];
        this.accessCode5Ref.current.value = text[4];
        this.accessCode6Ref.current.value = text[5];
    }
    render() {
        let errorDisplay = (<div></div>);
        if (this.state.error) {
            errorDisplay = (
                <Alert className='message' bsStyle="danger">
                    {this.state.error}
                </Alert>);
        } else if (this.state.info) {
            errorDisplay = (
            <Alert className='message' bsStyle = "info">
                {this.state.info}
            </Alert>);
        }
        return(
            <Modal.Dialog>
                <Modal.Header>
                    <Modal.Title>Enter your access code</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <form>
                        <Grid>
                            <Row className="show-grid">
                                <Col xs={12} md={12}  lg={12} >
                                    <div>
                                        Enter the access code that was emailed to you. It may have gone to your spam folder.
                                    </div>
                                    <div  className="resend" onClick={() => { this.onResendEmail(); }}> Resend the email.</div>
                                </Col>
                            </Row>
                            <Row className="show-grid">
                                <Col xs={12} md={12}  lg={12} >
                                    <div className='input-row'>
                                        <input
                                            autoFocus
                                            type='text'
                                            className='code-input'
                                            ref={this.accessCode1Ref}
                                            maxLength='1'
                                            size = '3' tabIndex='1'
                                            onPaste={(event) => { this.onPaste( event, this.accessCode1Ref ); }}

                                        />
                                        <input
                                            type='text'
                                            className='code-input'
                                            ref={this.accessCode2Ref}
                                            maxLength='1'
                                            size = '3'
                                            tabIndex='2'
                                            onPaste={(event) => { this.onPaste( event, this.accessCode2Ref ); }}
                                        />
                                        <input
                                            type='text'
                                            className='code-input'
                                            ref={this.accessCode3Ref}
                                            maxLength='1'
                                            size = '3'
                                            tabIndex='3'
                                            onPaste={(event) => { this.onPaste( event, this.accessCode3Ref ); }}
                                        />
                                        <input
                                            type='text'
                                            className='code-input'
                                            ref={this.accessCode4Ref}
                                            maxLength='1'
                                            size = '3'
                                            tabIndex='4'
                                            onPaste={(event) => { this.onPaste( event, this.accessCode4Ref ); }}
                                        />
                                        <input
                                            type='text'
                                            className='code-input'
                                            ref={this.accessCode5Ref}
                                            maxLength='1'
                                            size = '3'
                                            tabIndex='5'
                                            onPaste={(event) => { this.onPaste( event, this.accessCode5Ref ); }}
                                        />
                                        <input
                                            type='text'
                                            className='code-input'
                                            ref={this.accessCode6Ref}
                                            maxLength='1'
                                            size = '3'
                                            tabIndex='6'
                                            onPaste={(event) => { this.onPaste( event, this.accessCode6Ref ); }}
                                        />
                                    </div>
                                </Col>
                            </Row>
                            <Row className="show-grid">
                                <Col xs={12} md={12}  lg={12} >
                                    <div className='message-row'>
                                        {errorDisplay}
                                    </div>
                                </Col>
                            </Row>
                        </Grid>
                    </form>
                </Modal.Body>
                <Modal.Footer>
                    <Button onClick={() => { this.onCancelAccessCode(); }}>Close</Button>
                    <Button onClick={() => { this.onSubmitAccessCode(); }} bsStyle="primary">Submit</Button>
                </Modal.Footer>
            </Modal.Dialog>
        );
    }
}


