import Comment from './comment';
import _ from 'lodash';

export default class RootComment {
    constructor(data) {
        this.init();
    }

    init() {
        this._children = [];
        this._id = window.location.href;
    }

    setData(data) {
        if (data._id) this.id = data._id;
        if (data.children) this._children = data.children;
    }

    get id() { return this._id; }

    addChildFromRawCommentList(rawCommentList, depth, parentId, recursive) {
        console.log('PARLER LOGGING addChildFromRawCommentList: depth: ' + depth + ', parentId: ' + parentId);
        if ((!rawCommentList) || (!rawCommentList.length) || (!(rawCommentList instanceof Array)))
            return false;
        let actualDepth = (( depth )? depth : 0 );
        for (let loop = 0; loop < rawCommentList.length; loop++) {
            let comment = rawCommentList[loop];
            let commentDepth = parseInt(comment.commentDepth, 10);
            if ((comment.parent !== parentId) || (commentDepth !== actualDepth)) continue;
            let childComment = new Comment(comment);
            childComment.setParent(this, false);
            if ( recursive ) {
                childComment.addChildFromRawCommentList(rawCommentList, actualDepth + 1, childComment.id, recursive);
            }
        }

        this._children = _.sortBy(this._children, [function(o) { return o._createdAt; }]).reverse();
        return true;
    }
    getCommentById(idRegex) {
        let regEx = idRegex;
        if ('string' === typeof idRegex) {
            regEx = new RegExp(idRegex);
        }
        if (regEx.test(this._id)) {
            return this;
        }
        for (let loop = 0; loop < this._children.length; loop++) {
            let comment = this._children[loop];
            let result = comment.getCommentById(regEx);
            if (result) return result;
        }
        return null;
    }
    isValid() {
        return true;
    }
}



