import React from 'react';
import {Modal, Grid, Row, Col, Button, Alert} from 'react-bootstrap';
import validator from 'validator';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/css/bootstrap-theme.css';
import './login-email.css';

export default class LoginEmail extends React.Component {
    constructor(props) {
        super(props);
        this.emailRef = React.createRef();
        this.state = {error: ''};
    }
    onSubmitEmail() {
        const email = this.emailRef.current.value;
        if (!validator.isEmail(email)) {
            this.setState(...this.state, {error: 'Please enter a valid email address.'});
            return;
        }
        this.props.emailSubmitFunc(email);
    }
    onCancelEmail() {
        this.props.emailCancelFunc();
    }
    render() {
        let errorDisplay = (<div></div>);
        if (this.state.error) {
            errorDisplay = (
                <Alert className='message'  bsStyle="danger">
                    {this.state.error}
                </Alert>);
        }
        return (
            <Modal.Dialog>
                <Modal.Header>
                    <Modal.Title>Enter your email address</Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <form>
                        <Grid>
                            <Row className="show-grid">
                                <Col xs={12} md={12}  lg={12} >
                                    <div>
                                        An access code will be sent to your email address.
                                    </div>
                                </Col>
                            </Row>
                            <Row className="show-grid">
                                <Col xs={12} md={12}  lg={12} >
                                    <input className="email-input"
                                           ref={this.emailRef}
                                           autoFocus
                                           type='text'
                                           maxLength='100'
                                           placeholder={'email'}
                                           size = '80'
                                           tabIndex='1'/>
                                </Col>
                            </Row>
                            <Row className="show-grid">
                                <Col xs={12} md={12}  lg={12} >
                                    {errorDisplay}
                                </Col>
                            </Row>
                        </Grid>
                     </form>
                </Modal.Body>
                <Modal.Footer>
                    <Button onClick={() => { this.onCancelEmail(); }}>Close</Button>
                    <Button onClick={() => { this.onSubmitEmail(); }} bsStyle="primary">Submit</Button>
                </Modal.Footer>
            </Modal.Dialog>
        );
    }
}


