import React from 'react';

export default class SVGButton  extends React.Component {

    render() {
        return (
            <span className={this.props.containerStyle} onClick={() => { this.click(); }}>
                <span className={this.props.innerContainerStyle}>
                    <img className={this.props.svgStyle} src={this.props.svgGraphicFile} alt={'this.props.title'}/>
                </span>
                <span className={this.props.titleStyle}>{this.props.title}</span>
            </span>
        );
    }

    click() {
        let id = this.props.controlId;
        let message = this.props.clickMessage;
        this.props.clickFunc(id, message);
    }
}



