// const STYLE_deleteInnerContainer = 'deleteInnerContainer';
// const STYLE_deleteSVG= 'deleteSVG';
// const STYLE_deleteTitle= 'deleteTitle';
const STYLE_commentEditorPanel = 'commentEditorPanel';
const STYLE_commentEditorPanelHidden = 'commentEditorPanelHidden';

export default class ControlBar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {};
        this.state.upvotes = this.props.commentData.upvotes;
        this.state.upvoted = this.props.commentData.upvoted;
        this.state.downvotes = this.props.commentData.downvotes;
        this.state.downvoted = this.props.commentData.downvoted;
        this.state.votes = this.props.commentData.upvotes - this.props.commentData.downvotes;
        this.state.commentEditorPanelStyle = STYLE_commentEditorPanelHidden;
    }

    handleUpvoteClick(id, message, votes) {
        if (this.state.upvoted) {
            this.props.deleteVoteFunc(true);
        } else {
            this.props.addVoteFunc(true);
        }
        let newState = {
            ...this.state,
            votes: votes - this.state.downvotes,
            upvotes: votes,
            upvoted: !this.state.upvoted
        };
        this.setState(newState);
    }
    handleDownvoteClick(id, message, votes) {
        if (this.state.downvoted) {
            this.props.deleteVoteFunc(false);
        } else {
            this.props.addVoteFunc(false);
        }
        let newState = {
            ...this.state,
            votes: this.state.upvotes - votes,
            downvotes: votes,
            downvoted: !this.state.downvoted
        };
        this.setState(newState);
    }
    handleEchoClick() {
        this.props.echoCommentFunc();

    }
    handleCommentClick() {
        let newState = {
            ...this.state,
            commentEditorPanelStyle: STYLE_commentEditorPanel
        };
        this.setState(newState);
    }
    addComment(comment){
        this.props.addCommentFunc(comment, this.props.commentData.id);
        let newState = {
            ...this.state,
            commentEditorPanelStyle: STYLE_commentEditorPanelHidden
        };
        this.setState(newState);
    }
    render() {
        return (
            <span>
                <span className={STYLE_controlBar}>
                    <SVGButton
                        containerStyle = {STYLE_commentContainer}
                        innerContainerStyle = {STYLE_commentInnerContainer}
                        titleStyle = {STYLE_commentTitle}
                        svgStyle = {STYLE_commentSVG}
                        svgGraphicFile = 'http://parler.com/assets/Comment.svg'
                        title = 'Comment'
                        controlId = '3'
                        clickMessage = 'Comment'
                        clickFunc = { () => { this.handleCommentClick(); }}
                    />
                    <div className='viewReplies'>View Replies</div>
                </span>
                <span className={this.state.commentEditorPanelStyle}>
                    <CommentEditor addCommentFunc={(comment) => { this.addComment(comment); }}/>
                </span>
            </span>
        );
    }
}

