import React from 'react';
import './svg-vote-button.css';

export default class SVGVoteButton  extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            commentData: this.props.commentData,
            upvoteImage: 'http://parler.com/assets/Like.png',
            downvoteImage: 'http://parler.com/assets/Unlike.png',
        };
    }

    render() {
        return (
            <span className={'voteContainer'}>
                <span className={'upvoteInnerContainer'} onClick={() => { this.clickUpvote(); }}>
                    <img className={'upvoteSVG'} src={this.state.upvoteImage} alt={'upvote'}/>
                </span>
                <span className={'voteCount'}>{this.state.commentData.score}</span>
                <span className={'downvoteInnerContainer'} onClick={() => { this.clickDownvote(); }}>
                    <img className={'downvoteSVG'} src={this.state.downvoteImage} alt={'downvote'}/>
                </span>
            </span>
        );
    }

    clickUpvote() {
        this.props.upvoteFunc();
        if ('http://parler.com/assets/Like.png' === this.state.upvoteImage) {
            this.setState({
                ...this.state,
                upvoteImage: 'http://parler.com/assets/Like-red.png',
                downvoteImage: 'http://parler.com/assets/Unlike.png'
            });
        } else {
            this.setState({
                ...this.state,
                upvoteImage: 'http://parler.com/assets/Like.png',
                downvoteImage: 'http://parler.com/assets/Unlike.png'
            });
        }
    }
    clickDownvote() {
        this.props.downvoteFunc();
        if ('http://parler.com/assets/Unlike.png' === this.state.downvoteImage) {
            this.setState({
                ...this.state,
                upvoteImage: 'http://parler.com/assets/Like.png',
                downvoteImage: 'http://parler.com/assets/Unlike-red.png'
            });
        } else {
            this.setState({
                ...this.state,
                upvoteImage: 'http://parler.com/assets/Like.png',
                downvoteImage: 'http://parler.com/assets/Unlike.png'
            });
        }
    }
}



