import React from 'react';
import Parler from './index';

export default class BlobImage extends React.Component {
    DEFAULT_PROFILE_PHOTO = 'http://parler.com/assets/defaultUserIcon.jpg';

    constructor(props) {
        super(props);
        let imageURL = this.props.apiURL;
        if (-1 !== imageURL.indexOf('id=null')) imageURL = this.DEFAULT_PROFILE_PHOTO;
        this.state = { imageURL: imageURL  };
    }
    componentWillMount() {
        this.downloadImage();
     }
    downloadImage() {
        console.log("XXXXXXXXXX this.DEFAULT_PROFILE_PHOTO: " + this.DEFAULT_PROFILE_PHOTO);
        const self = this;
        let xhr = new XMLHttpRequest();
        function response(res) {
            if (200 !== xhr.status) {
                if (self.DEFAULT_PROFILE_PHOTO !== self.state.imageURL) {
                    console.log('PARLER LOGGING BAD STATUS: ' + xhr.status);
                    self.setState({imageURL: self.DEFAULT_PROFILE_PHOTO});
                    self.downloadImage();
                    return;
                }
            }
            const urlCreator = window.URL || window.webkitURL;
            const imageUrl = urlCreator.createObjectURL(this.response);
            self.setState({ imageURL: imageUrl });
            console.log('PARLER LOGGING IMAGE LOADED: ' + Parler.debugPrint(self.props.apiURL, false) + ", xhr.status: " + xhr.status);
        }
        function error(err) {
            console.log('PARLER LOGGING IMAGE ERROR ' + Parler.debugPrint(err, false));
            if (self.DEFAULT_PROFILE_PHOTO !== self.state.imageURL) {
                self.setState({imageURL: self.DEFAULT_PROFILE_PHOTO});
                self.downloadImage();
            }
        }

        xhr.open('GET', this.state.imageURL);
        xhr.responseType = 'blob';
        xhr.onload = response;
        xhr.onerror = error;
        // TODO:
        xhr.send();
    }
    render() {
        return (
            <img
                className = {this.props.pictureClassName}
                src = {this.state.imageURL}
                alt = {this.props.altText}
            />
        );
    }
}


