import React from 'react';
import CommentDropdownComponent from "./comment-dropdown-component";
import './comment-dropdown-component';
import './comment-header.css';
import './glyphicons_pro_1_9_2/glyphicons/web/html_css/css/glyphicons.css';
import './glyphicons_pro_1_9_2/glyphicons/web/html_css/css/style.css';
import './glyphicons_pro_1_9_2/glyphicons/web/html_css/fonts/glyphicons-regular.woff2';

export default class CommentHeader extends React.Component {
    editComment() {
        console.log("XXXXXXXXXX editComment");
    }
    deleteComment() {
        console.log("XXXXXXXXXX deleteComment");
        this.props.deleteCommentFunc();
    }
    goAnonymous() {
        console.log("XXXXXXXXXX goAnonymous");
    }
    render() {
        let creatorUserName = ((this.props.commentData.creatorUserName)? this.props.commentData.creatorUserName : this.props.commentData.createdUserData.username );
        let userVerified = [];
        if ((!this.props.commentData.deleted)
        && (this.props.commentData.createdUserData.verified)) {
            userVerified.push(<img className={'userVerified'} src="http://parler.com/assets/verified.png"/>);
        } else {
            userVerified.push(<div/>);
        }
        let commentPublicPrivate = [];
        if ((this.props.commentData.viewingUserId === this.props.commentData.createdUserId)
        && (!this.props.commentData.userIsPrivate)){
            commentPublicPrivate.push(
                <div className={'commentHeaderPublicPrivateWrapper'}>
                    <span className={'dropdownPadding'}/>
                    <img className={'chatIcon'} src="http://parler.com/assets/chat.png"/>
                    <span className={'publicComment'}> Public comment</span>
                </div>);
        } else {
            commentPublicPrivate.push(<div/>);
        }
        return (
            <div className={'commentHeader'}>
                <div className={'commentHeaderPublicDropdownAndDate'}>
                    { commentPublicPrivate }
                    <div id="commentHeaderDropdownAndDate" className={'commentHeaderDropdownAndDate'}>
                        <div id="commentHeaderDate" className={'commentHeaderDate'}>{this.props.commentData.createdAtFormatted}</div>
                        <CommentDropdownComponent id="commentHeaderDropdown" className={'commentHeaderDropdown'}
                            editFunc={() => { this.editComment(); }}
                            deleteFunc={() => { this.deleteComment(); }}
                            goAnonymousFunc={() => { this.goAnonymous(); }}
                            commentData={this.props.commentData}
                        />
                    </div>
                </div>
                <div className={'commentHeaderDropdownAndDate'}>
                    <div className={'commentHeaderUser'}>{creatorUserName}</div>
                    {userVerified}
                </div>
            </div>);
    }
}


