import RootComment from './root-comment';

export default class Comment extends RootComment {

    static formatCommentDate(comment) {
        let result = '';
        if (!comment) return result;
        let createdAtYear = comment.createdAt.substr(0, 4);
        let createdAtMonth = comment.createdAt.substr(4, 2);
        let createdAtDay = comment.createdAt.substr(6, 2);
        let createdAtHour = comment.createdAt.substr(8, 2);
        let createdAtMinute = comment.createdAt.substr(10, 2);
        let createdAtSecond = comment.createdAt.substr(12, 2);
        let commentDate = new Date(createdAtYear, parseInt(createdAtMonth, 10) - 1, createdAtDay, createdAtHour, createdAtMinute, createdAtSecond);
        let newDate = new Date();
        let now = newDate.toDateString();
        let then = commentDate.toDateString();
        let hours = ((12 < commentDate.getHours()) ? commentDate.getHours() - 12 : commentDate.getHours());
        let minutes = ((10 > commentDate.getMinutes()) ? '0' + commentDate.getMinutes() : commentDate.getMinutes());
        let am = ((12 < commentDate.getHours()) ? ' PM' : ' AM');
        let time = hours + ':' + minutes + am;
        if (now === then) {
            if ((newDate.getHours() === commentDate.getHours())
                && (newDate.getMinutes() === commentDate.getMinutes())) {
                result = 'Just now';
            } else {
                result = 'Today ' + time;
            }
        } else {
            result = then;
        }
        return result;
    };

    constructor(data) {
        super(data);
        if (data)
            this.setData(data);
    }

    get id() {
        return this._id;
    }

    set id(inId) {
        this._id = inId;
    }

    get body() {
        return this._body;
    }

    set body(inBody) {
        this._body = inBody;
    }
    get verified() {
        return this._verified;
    }

    set verified(inVerified) {
        this._verified = inVerified;
    }

    get commentDepth() {
        return this._commentDepth;
    }

    set commentDepth(inCommentDepth) {
        this._commentDepth = inCommentDepth;
    }

    get depth() {
        return this._depth;
    }

    set depth(inDepth) {
        this._depth = inDepth;
    }

    get depthRaw() {
        return this._depthRaw;
    }

    set depthRaw(inDepthRaw) {
        this._depthRaw = inDepthRaw;
    }

    get rank() {
        return this._rank;
    }

    set rank(inRank) {
        this._rank = inRank;
    }

    get score() {
        return this._score;
    }

    set score(inScore) {
        this._score = inScore;
    }

    get upvotes() {
        return this._upvotes;
    }

    set upvotes(inUpvotes) {
        this._upvotes = inUpvotes;
        this.score = this.upvotes - this.downvotes;
    }

    get upvoted() {
        return this._upvoted;
    }

    set upvoted(inUpvoted) {
        this._upvoted = inUpvoted;
    }

    get downvotes() {
        return this._downvotes;
    }

    set downvotes(inDownvotes) {
        this._downvotes = inDownvotes;
        this.score = this.upvotes - this.downvotes;
    }

    get downvoted() {
        return this._downvoted;
    }

    set downvoted(inDownvoted) {
        this._downvoted = inDownvoted;
    }

    get reposts() {
        return this._reposts;
    }

    set reposts(inReposts) {
        this._reposts = inReposts;
    }

    get comments() {
        return this._comments;
    }

    set comments(inComments) {
        this._comments = inComments;
    }

    get profilePhotoId() {
        return this._profilePhotoId;
    }

    set profilePhotoId(inProfilePhotoID) {
        this._profilePhotoId = inProfilePhotoID;
    }

    get createdAt() {
        return this._createdAt;
    }

    get createdAtFormatted() {
        return Comment.formatCommentDate(this);
    }

    set createdAt(inCreatedAt) {
        this._createdAt = inCreatedAt;
    }

    get creatorUserName() {
        if (this.deleted) return 'COMMENT DELETED BY USER';
        return this._creatorUserName;
    }

    set creatorUserName(inCreatorUserName) {
        this._creatorUserName = inCreatorUserName;
    }

    get deleted() {
        return this._deleted;
    }

    set deleted(inDeleted) {
        this._deleted = inDeleted;
    }

    get creatorDeleted() {
        return this._creatorDeleted;
    }

    set creatorDeleted(inCreatorDeleted) {
        this._creatorDeleted = inCreatorDeleted;
    }

    get viewingUserId() {
        return this._viewingUserId;
    }

    set viewingUserId(inViewingUserId) {
        this._viewingUserId = inViewingUserId;
    }

    get post() {
        return this._post;
    }

    set post(inPost) {
        this._post = inPost;
    }

    get parent() {
        return this._parent;
    }

    get parentCommentRecord() {
        return this._parentCommentRecord;
    }

    init() {
        super.init();
        this._verified = false;
        this._deleted = false;
        this._score = 0;
        this._upvotes = 0;
        this._downvotes = 0;
        this._downvotes = 0;
        this._reposts = 0;
        this._comments = 0;
        this._upvoted = false;
        this._downvoted = false;
        this._commentDepth = 0;
        this._depth = 1;
        this._depthRaw = 1;
    }

    setData(data) {
        super.setData(data);
        if (!data) return;
        if (data.verified) this.verified = data.verified;
        if (data.body) this.body = data.body;
        if (data.commentDepth) this.commentDepth = data.commentDepth;
        if (data.depth) this.depth = data.depth;
        if (data.depthRaw) this.depthRaw = data.depthRaw;
        if (data.rank) this.rank = data.rank;
        if (data.score) this.score = data.score;
        if (data.upvotes) this.upvotes = data.upvotes;
        if (data.upvoted) this.upvoted = data.upvoted;
        if (data.downvotes) this.downvotes = data.downvotes;
        if (data.downvoted) this.downvoted = data.downvoted;
        if (data.reposts) this.reposts = data.reposts;
        if (data.comments) this.comments = data.comments;
        if (data.profilePhotoId) this.profilePhotoId = data.profilePhotoId;
        if (data.createdAt) this.createdAt = data.createdAt;
        if (data.creatorUserName) this.creatorUserName = data.creatorUserName;
        if (data.deleted) this.deleted = data.deleted;
        if (data.creatorDeleted) this.creatorDeleted = data.creatorDeleted;
        if (data.viewingUserId) this.viewingUserId = data.viewingUserId;
        if (data.post) this.post = data.post;
        if (data.parent) this._parent = data.parent;
    }

    upvote() {
        if (!this.upvoted) {
            this.upvotes = parseInt(this.upvotes, 10) + 1;
            this.upvoted = true;
            if (this.downvoted) {
                this.downvotes = parseInt(this.downvotes, 10) - 1;
                this.downvoted = false;
            }
        } else {
            this.upvotes = parseInt(this.upvotes, 10) - 1;
            this.upvoted = false;
        }
        this.score = parseInt(this.upvotes, 10) - parseInt(this.downvotes, 10);
    }

    downvote() {
        if (!this.downvoted) {
            this.downvotes = parseInt(this.downvotes, 10) + 1;
            this.downvoted = true;
            if (this.upvoted) {
                this.upvotes = parseInt(this.upvotes, 10) - 1;
                this.upvoted = false;
            }
        } else {
            this.downvotes = parseInt(this.downvotes, 10) - 1;
            this.downvoted = false;
        }
        this.score = parseInt(this.upvotes, 10) - parseInt(this.downvotes, 10);
    }

    incrimentCommentCount() {
        this.comments =  this.comments + 1;
        if (this._parentCommentRecord) {
            this._parentCommentRecord.comments = this._parentCommentRecord.comments + 1;
        }
    }

    isValid() {
        if (!this._id) return false;
        if (!this.body) return false;
        if ((!this.commentDepth) && (0 !== this.commentDepth)) return false;
        if ((!this.depth) && (0 !== this.depth)) return false;
        if ((!this.score) && (0 !== this.score)) return false;
        if ((!this.upvotes) && (0 !== this.upvotes)) return false;
        if ((!this.downvotes) && (0 !== this.downvotes)) return false;
        if ((!this.reposts) && (0 !== this.reposts)) return false;
        if ((!this.comments) && (0 !== this.comments)) return false;
        if ((!this.upvoted) && (false !== this.upvoted)) return false;
        if ((!this.downvoted) && (false !== this.downvoted)) return false;
        if (!this.profilePhotoId) return false;
        if (!this.createdAt) return false;
        if ((!this.deleted) && (false !== this.deleted)) return false;

        return true;
    }

    setParent(parent, incrimentCommentCount) {
        if ((!parent) || (!parent.isValid())) {
            console.log('PARLER LOGGING setParent failed due to invalid parent.');
            return false;
        }
        if (!this.isValid()) {
            console.log('PARLER LOGGING setParent failed due to invalid child.');
            return false;
        }
        parent._children.unshift(this);
        this._parent = parent.id;
        this._parentCommentRecord = parent;
        if ((incrimentCommentCount) && (this._parentCommentRecord.incrimentCommentCount)) {
            this._parentCommentRecord.incrimentCommentCount();
        }
        return true;
    }

    getRoot() {
        if (!this.parentCommentRecord) {
            return this;
        }
        else if (this.parentCommentRecord.parent) {
            return this.parentCommentRecord.getRoot();
        }
        else {
            return this.parentCommentRecord;
        }
    }
}


