import React from 'react';
import {Button} from 'react-bootstrap';
import './comment-editor.css';

export default class CommentEditor extends React.Component {
    constructor(props) {
        super(props);
        this.state = { text: '', buttonClass: 'commentEditorSubmitButtonHidden' };
        this.myRef = React.createRef();
    }
    click(e) {
        let comment = this.myRef.current.value;
        this.myRef.current.value = '';
        this.props.addCommentFunc(comment);
        this.setState({
            text: '',
            buttonClass: ((0 < e.target.value.length)? 'commentEditorSubmitButton' : 'commentEditorSubmitButtonHidden')
        });
    }
    keyUp(e) {
        this.setState({
            text: e.target.value,
            buttonClass: ((0 < e.target.value.length)? 'commentEditorSubmitButton' : 'commentEditorSubmitButtonHidden')
        });
    }
    render() {
        return (
            <div className={'commentEditorInnerPanel'}>
                <form className={'commentEditorForm'}>
                    <textarea className={'commentEditorText'}
                              ref={this.myRef}
                              placeholder={'What\'s on your mind?'}
                              maxLength = '1000'
                              rows = '3'
                              onKeyUp={(e) => { this.keyUp(e); }}/>
                </form>
                <div className={'commentEditorFooter'}>
                    <div className={'commentEditorCharacterCount'}>{this.state.text.length}/1000</div>
                    <Button className={this.state.buttonClass} bsStyle="danger" onClick={(e) => { this.click(e); }}>Post</Button>
                </div>
            </div>);
    }
}


