import React from 'react';
import ProfilePic from './profile-pic';
import CommentHeader from './comment-header';
import ControlBar from './control-bar';
import SVGVoteButton from './svg-vote-button';
import './comment-component.css';
import './comment-header.css';

const STYLE_commentRow = 'commentRow';
const STYLE_commentInset = 'commentInset';
const STYLE_commentPanel = 'commentPanel';
const STYLE_commentBody = 'commentBody';

export default class CommentComponent extends React.Component {
    constructor(props) {
        super(props);
        this.state = { commentData: this.props.commentData };
    }
    deleteComment() {
        this.props.deleteCommentFunc(this.props.commentData._id);
    }
    addComment(comment){
        this.props.addCommentFunc(comment, this.props.commentData._id);
    }
    echoComment(){
        this.props.echoCommentFunc(this.props.commentData._id, this.props.commentData.parent);
    }
    upvote() {
        this.props.upvote(this.props.commentData);
    }
    downvote() {
        this.props.downvote(this.props.commentData);
    }
    vote(upvote) {
        this.props.addVoteFunc(this.props.commentData._id, upvote);
        this.setState( {...this.state, commentData: this.props.commentData } );
    }
    deleteVote(upvote) {
        this.props.deleteVoteFunc(this.props.commentData._id, upvote);
        this.setState( {...this.state, commentData: this.props.commentData } );
    }
    render() {
        let insets = [];
        let depth = parseInt(this.props.commentData.commentDepth, 10);
        for (let loop = 0; loop < depth; loop++) {
            insets.push(<div className={STYLE_commentInset} key={loop}>{depth}</div>)
        }
        console.log("XXXXXXXXXXX this.props.apiURL: " + this.props.apiURL);
        return (
            <div className={STYLE_commentRow}>
                <SVGVoteButton
                    upvoteFunc={() => { this.upvote(); }}
                    downvoteFunc={() => { this.downvote(); }}
                    commentData={this.state.commentData}
                />
                {insets}
                <ProfilePic pictureId={this.state.commentData.profileImageId}
                            apiURL={this.props.apiURL}
                />
                <div className={STYLE_commentPanel}>
                    <CommentHeader
                        commentData={this.props.commentData}
                        deleteCommentFunc={() => { this.deleteComment(); }}
                    />
                    <div className={STYLE_commentBody}>{this.props.commentData.body}</div>
                    <ControlBar
                        commentData={this.props.commentData}
                        addCommentFunc={(comment) => { this.addComment(comment); }}
                        echoCommentFunc={() => { this.echoComment(); }}
                    />
                </div>
            </div>);
   }
}


